import React, { useState, useCallback, useRef } from "react";
import { Row, Col } from "reactstrap";

export default function Account(props) {
  const { bridal, groom, icon } = props.config;
  const [displayState, setDisplayState] = useState(false);

  const copyText = useRef();
  const scrollRef = useRef();

  const copy = () => {
    const el = copyText.current;
    console.log(el);
    el.select();
    document.execCommand("copy");
  };

  const doCopy = (text) => {
    //  1.
    if (!document.queryCommandSupported("copy")) {
      return alert("   .");
    }
    //  2.
    const textarea = document.createElement("textarea");
    textarea.value = text;
    textarea.style.top = 0;
    textarea.style.left = 0;
    textarea.style.position = "fixed";
    //  3.
    document.body.appendChild(textarea);
    // focus() ->   
    textarea.focus();
    // select() ->       
    textarea.select();
    //  4.
    document.execCommand("copy");
    //  5.
    document.body.removeChild(textarea);
    alert(" .");
  };

  const onClickAccountBtn = () => {
    setDisplayState(!displayState);
  };

  const scrollToBottom = useCallback(() => {
    console.log("d");
    if (displayState) {
      //  
      scrollRef.current.scrollIntoView({
        behavior: "smooth",
        block: "end",
        inline: "nearest",
      });
      console.log("d");
    }
  }, [displayState]);

  return (
    <Col className="mt-5">
      <Row className="mt-5">
        <Col>
          <p className="txt-title mb-3 pb-3">   </p>
        </Col>
      </Row>
      <Row>
        <Col className="mb-3 txt-middle">
          <p className="mb-1">  </p>
          <p className="mb-1"> </p>
          <p className="mb-1">   .</p>
          <p className="mb-1">.</p>
        </Col>
      </Row>
      <Row>
        <Col className="mb-5">
          <img className="rounded-circle flower-icon" src={icon.flower_pink} />
        </Col>
      </Row>
      <Row className="btn-account mb-1">
        <Col className="pt-2 pb-2 pr-3">
          <a onClick={onClickAccountBtn}>  </a>
        </Col>
      </Row>
      {/* <a className="btn-icon" role="button" href={doCopy("d")}>
                  <FontAwesome name="phone" />
                </a> */}
      {displayState ? (
        <div className="account">
          <Row className="mb-1 txt-left">
            <Col className="pb-2 pr-3">
              <p className="mb-3 txt-head">{bridal.role}</p>

              <p className="mb-1 txt-middle">
                {bridal.motherAccount} ({bridal.mother})
              </p>
              <p className="mb-1 txt-middle">
                {bridal.account} ({bridal.name})
              </p>
            </Col>
          </Row>
          <Row className="mb-1 txt-left">
            <Col className="pt-2 pb-2 pr-3">
              <p className="mb-3 txt-head">{groom.role}</p>
              <p className="mb-1 txt-middle">
                {groom.motherAccount} ({groom.mother})
                {/* <Button className="btn-copy" onClick={copy}>
                  copy
                </Button> */}
              </p>
              <p className="mb-1 txt-middle">
                {groom.fatherAccount} ({groom.father})
              </p>
              <p className="mb-1 txt-middle">
                {groom.account} ({groom.name})
              </p>
            </Col>
          </Row>
        </div>
      ) : (
        ""
      )}
      <Row ref={scrollRef}></Row>
    </Col>
  );
}



