import React from "react";
import { Row, Col } from "reactstrap";

// import { Map } from "../Component";

export default function Place(props) {
  const { name, latitude, longitude, image } = props.config.wedding.place;
  return (
    <Row className="mt-5">
      <Col className="mt-5">
        <Row>
          <Col>
            <p className="txt-title"> </p>
          </Col>
        </Row>
        <Row>
          <Col>
            <img className="map-image" src={image} alt={`${name}`} />
          </Col>
        </Row>
        <Row>
          <Col className="pt-2 pb-2 pr-3 mb-5">
            <a
              href={`navermaps://?menu=location&pinType=place&lat=${latitude}&lng=${longitude}&title=${name}`}
              className="btn btn-naver btn-block"
            >
               
            </a>
          </Col>
        </Row>
        <Row className="mb-5 txt-left">
          <Col>
            <p className="mb-1 txt-head"> </p>
            <p className="mb-3">Tel. 02-6081-5000</p>
            <p className="mb-1 txt-head">  </p>
            <p className="mb-3">3() 6  300M  5</p>
            <p className="mb-1 txt-head">  </p>
            <p className="mb-3">
              40, 4312, 9404, 6009, 145, 148, 441, 542, 4212, 3030, 08,
              03
            </p>
            <p className="mb-1 txt-head"> </p>
            <p className="mb-1"> </p>
            <p className="mb-1">)   652 </p>
            <p className="mb-1">)   504-11</p>
          </Col>
        </Row>
      </Col>
    </Row>
  );
}



