import React from "react";
import { Row, Col } from "reactstrap";
import FontAwesome from "react-fontawesome";

export default function Contact(props) {
  const { role, image_icon, father, mother, fatherPhone, motherPhone } =
    props.profile;
  return (
    <div>
      <Row className="pt-3">
        <Col>
          <img className="rounded-circle contact-icon" src={image_icon} />
        </Col>
      </Row>
      <Row className="pt-4 txt-head">
        <Col>
          <p className="mb-3 txt-head">{role}   </p>
        </Col>
      </Row>
      {motherPhone ? (
        <Row className="mb-3">
          <Col>
            <p className="mb-1 txt-middle"> {mother}</p>
          </Col>
          <Col>
            <a className="btn-icon" role="button" href={`tel:${motherPhone}`}>
              <FontAwesome name="phone" />
            </a>
          </Col>
        </Row>
      ) : (
        ""
      )}
      {fatherPhone ? (
        <Row className="mb-1">
          <Col>
            <p className="mb-1 txt-middle"> {father}</p>
          </Col>
          <Col>
            <a className="btn-icon" role="button" href={`tel:${fatherPhone}`}>
              <FontAwesome name="phone" />
            </a>
          </Col>
        </Row>
      ) : (
        ""
      )}
    </div>
  );
}



